import {onReady, WFRoute} from "@xatom/core";
import Alpine from 'alpinejs'
import {
    AnswerType,
    baserowClient,
    baserowNestedType,
    baserowType,
    QuestionsType,
    RoomsType,
    ServicesType
} from "./modules/baserow";
import "@deltaclan/superform"


new WFRoute("/estimation").execute(() => {
    console.log("Estimation route executed");
    onReady(() => {
        window.Alpine = Alpine

        Alpine.data('multiStepForm', () => {
            return {
                questions: [] as QuestionsType[],
                services: [] as ServicesType[],
                rooms: [] as RoomsType[],
                selectedService: null as ServicesType | null,
                currentStep: 0,
                finalStep: 'step-contact',
                stepRoomSize: 'step-rooms-size',
                selectedRooms: [] as ({roomId: number, room: RoomsType, count: number})[],
                roomError: false,
                loaded: false,
                estimation: 0,
                estimationStatic: 0,
                budget: {
                    service: "",
                    rooms: [] as string[],
                    details: [],
                    total: 0
                },
                conditionalQuestions: [] as (QuestionsType & {passed: boolean})[],
                async init() {
                    await Promise.all([
                        this.fetchServices(),
                        this.fetchRooms(),
                        this.fetchQuestions()
                    ])



                    this.$nextTick(() => {
                        window.SuperformAPI = window.SuperformAPI || [];
                        window.SuperformAPI.push(() => {
                            const myForm = new Superform(document.querySelector("#superform"), {
                                debugMode: false
                            });

                            this.loaded = true

                            myForm.registerNavigationHook("questionGoBack", (params) => {
                                const { stepCount } = params;
                                const question = myForm.steps[stepCount].el
                                const questionId = parseInt(question.dataset.questionId)
                                this.setNotPassedQuestion(questionId, false)

                                return "back";
                            });

                            myForm.registerNavigationHook("roomGoBack", () => {
                                this.roomError = false
                                return "back";
                            });

                            myForm.registerNavigationHook("roomGoNext", () => {
                                this.roomError = false
                                this.selectedRooms = this.selectedRooms.filter((room) => room.count > 0)

                                if (this.selectedRooms.length === 0) {
                                    this.roomError = true
                                    return null
                                }

                                return this.stepRoomSize;
                            });

                            myForm.onStepChange( (step) => {
                                const currentQuestion = myForm.steps[step.stepCount].el
                                const currentQuestionId = parseInt(currentQuestion.dataset.questionId)
                                const notPassedQuestions = this.getNotPassedQuestions()

                                if (notPassedQuestions.length > 0) {
                                    if (currentQuestionId === notPassedQuestions[0].id) {
                                        this.setNotPassedQuestion(currentQuestionId)
                                    }
                                }
                            })

                            myForm.onFormSubmit((params, ev) => {
                                if (this.selectedService && this.selectedService.rooms && this.selectedService.rooms.length === 0) {
                                    this.selectedRooms = []
                                }

                                const totalRoomsSize = Object.entries(params.data)
                                    .filter(([key]) => key.startsWith("room-size-"))
                                    .reduce((sum, [, value]) => sum + parseFloat(value), 0);

                                if (params.data) {
                                    Object.entries(params.data).forEach(([key, value]) => {
                                        const input = myForm.inputManager.inputs.find((input => input.name === key && input.inputElement?.value === value))
                                        if (input && input.inputElement) {
                                            const inputAnswerId = parseInt(input.inputElement.dataset.answerId)

                                            if (inputAnswerId && !isNaN(inputAnswerId) && inputAnswerId > 0) {
                                                // Find the answer in the questions array
                                                const answer = this.findAnswerById(inputAnswerId)
                                                const question = this.findQuestionByAnswerId(inputAnswerId)

                                                if (answer && question) {
                                                    const price = parseFloat(answer.Price) || 0;

                                                    if (!isNaN(price) && price > 0) {
                                                        this.budget.details.push({
                                                            questionTitle: question.Title,
                                                            title: answer.value,
                                                            priceByUnit: price,
                                                            price: answer["Static Price"] ? price : price * totalRoomsSize,
                                                            isStatic: answer["Static Price"],
                                                        })
                                                    }
                                                }
                                            }
                                        }
                                    })
                                }

                                // Remove inputs that are not in params.data
                                myForm.inputManager.inputs.forEach((input => {
                                    if (!params.data[input.name]) {
                                        input.inputElement.remove()
                                    }
                                }))

                                this.budget.service = this.selectedService.Title
                                this.budget.rooms = this.selectedRooms.map((room) => room.room.Title)
                                this.budget.total = this.budget.details.reduce((total, item) => {
                                    return total + item.price;
                                }, 0)
                                const jsonString = JSON.stringify(this.budget);
                                const base64 = btoa(encodeURIComponent(jsonString));
                                localStorage.setItem("bp-budget", base64);

                                ev.preventDefault()
                                ev.stopPropagation()
                                ev.stopImmediatePropagation()
                            });
                        });
                    })

                },

                /**
                 * Set the conditional questions based on the answer given.
                 * * @param answer The answer given by the user.
                 * */
                setConditionalQuestions(answer: baserowNestedType & {"Next Question Id":baserowNestedType[]} = null) {
                    if (answer && answer["Next Question Id"].length > 0) {
                        if (this.$el.checked) {
                            if (this.conditionalQuestions.filter((question) => question.id === answer["Next Question Id"][0].id).length === 0) {
                                this.conditionalQuestions.push({...answer["Next Question Id"][0],passed:false})
                            }
                        } else {
                            this.conditionalQuestions = this.conditionalQuestions.filter((question) => question.id !== answer["Next Question Id"][0].id)
                        }
                    }
                },
                /**
                 * Get the conditional questions that are not already been passed.
                 * * @returns The conditional questions that are not passed.
                 * */
                getNotPassedQuestions() {
                    return this.conditionalQuestions.filter((question) => question.passed === false)
                },
                /**
                 * Set the conditional questions that are not passed.
                 * * @param id The id of the question to set as passed.
                 * * @param passed The boolean value to set as passed.
                 * */
                setNotPassedQuestion(id: number, passed: boolean = true) {
                    this.conditionalQuestions = this.conditionalQuestions.map((question) => {
                        if (question.id === id) {
                            return {...question,passed}
                        }
                        return question
                    })
                },
                /**
                 * Get the next question based on the current question and the answer given.
                 * * @param question The current question.
                 * * @param answer The answer given by the user.
                 * * @returns The next question to be displayed.
                 * */
                getNextQuestion(question: QuestionsType, answer: baserowNestedType & {"Next Question Id":baserowNestedType[]} = null) {
                    if (question.Type.value === "Radio" && answer) {
                        if (answer["Next Question Id"].length > 0) {
                            const nextQuestion = answer["Next Question Id"][0]
                            if (nextQuestion.id === 0) {
                                return null
                            }
                            return 'step-question-' + nextQuestion.id
                        }
                    }

                    const notPassedQuestions = this.getNotPassedQuestions()
                    if (notPassedQuestions.length > 0) {
                        const nextQuestion = notPassedQuestions[0]
                        if (nextQuestion.id === 0) {
                            return null
                        }
                        return 'step-question-' + nextQuestion.id
                    }

                    if (question["Next Question"].length > 0) {
                        const nextQuestion = question["Next Question"][0]
                        if (nextQuestion.id === 0) {
                            return null
                        }
                        return 'step-question-' + nextQuestion.id
                    }

                    return this.finalStep
                },
                findAnswerById(answerId: number): AnswerType | undefined {
                    return this.questions.flatMap(q => q.Answers).find(answer => answer.id === answerId);
                },
                findQuestionByAnswerId(answerId: number): QuestionsType | undefined {
                    return this.questions.find(q => q.Answers.some(answer => answer.id === answerId));
                },
                getServiceNextStep() {
                    if (this.selectedService && this.selectedService["First Question"].length > 0) {
                        const nextQuestion = this.selectedService["First Question"][0]

                        if (this.selectedService.Rooms.length === 0)
                            return 'step-question-' + nextQuestion.id

                        return 'step-rooms'
                    }

                    return this.finalStep
                },
                getRoomNextStep() {
                    if (this.selectedService && this.selectedService["First Question"].length > 0) {
                        const nextQuestion = this.selectedService["First Question"][0]

                        return 'step-question-' + nextQuestion.id
                    }
                    return this.finalStep
                },
                getSpecificSelectedRoomIndex(roomId: number) {
                    return this.selectedRooms.findIndex((roomItem: {roomId:number, count:number} & RoomsType ) => roomItem.roomId === roomId)
                },
                async fetchServices() {
                    const servicesData = await baserowClient.get<baserowType<ServicesType>>("/database/rows/table/669/?user_field_names=true#").fetch();
                    if (servicesData.results) {
                        this.services = servicesData.results
                    }
                },
                async fetchQuestions() {
                    const questionsData = await baserowClient.get<baserowType<QuestionsType>>("/database/rows/table/662/?user_field_names=true&Answers__join=Next Question Id,Price,Static Price#").fetch();
                    if (questionsData.results) {
                        this.questions = questionsData.results
                    }
                },
                async fetchRooms() {
                    const roomsData = await baserowClient.get<baserowType<RoomsType>>("/database/rows/table/670/?user_field_names=true#").fetch();
                    if (roomsData.results) {
                        this.rooms = roomsData.results
                    }
                },
            }
        })

        Alpine.data('counterRoomItem', (room: RoomsType) => {
            return {
                numberInput: 0,
                add() {
                    this.numberInput++
                },
                remove() {
                    if (this.numberInput >= 0) {
                        this.numberInput--
                    }
                },
                init() {
                    this.$watch('numberInput', (value) => {
                        const roomIndex = this.selectedRooms.findIndex((roomItem: {roomId:number, count:number} & RoomsType ) => roomItem.roomId === room.id)
                        if (roomIndex !== -1) {
                            if (value > 0) {
                                this.selectedRooms[roomIndex].count = value
                            } else {
                                this.selectedRooms.splice(roomIndex, 1)
                            }
                        } else {
                            this.selectedRooms.push({roomId: room.id, count: value, room})
                        }
                    })
                },
            }
        })

        Alpine.start()
    });
});

new WFRoute("/resultat-de-lestimation").execute(() => {
    console.log("Estimation result route executed");
    onReady(() => {
        window.Alpine = Alpine

        Alpine.data('estimationResult', () => {
            return {
                budget: {
                    service: "",
                    rooms: [] as string[],
                    details: [],
                    total: 0
                },
                init() {
                    console.log("Estimation result component initialized");
                    const budget = localStorage.getItem("bp-budget")
                    if (budget) {
                        const jsonString = decodeURIComponent(atob(budget))
                        this.budget = JSON.parse(jsonString)
                    }
                    console.log(this.budget)
                }
            }
        })
    })
});

